/**
 * 单元格对象
 */

var configModule = require('config')

var cellConfig = configModule.CellConfig

var Cell = function (coord) {

	//成员属性
	//设置默认值
	this.autoLF = false
	this.viewWidth = 0
	this.height = cellConfig.height
	this.width = cellConfig.width

	this.coord = coord
}

module.exports.Cell = Cell


